<template>
    <div class="login-wrapper">
        <a-form>
            <a-form-item v-bind="validateInfos.name">
                <a-input v-model:value="formData.name" @blur="validate('name', { trigger: 'blur' }).catch(() => {})">
                    <template #prefix>
                        <UserOutlined style="color:rgba(0,0,0,.25)" />
                    </template>
                </a-input>
            </a-form-item>
            <a-form-item v-bind="validateInfos.password">
                <a-input v-model:value="formData.password" type="password" @blur="validate('password', { trigger: 'blur' }).catch(() => {})">
                    <template #prefix>
                        <LockOutlined style="color:rgba(0,0,0,.25)" />
                    </template>
                </a-input>
            </a-form-item>
            <a-form-item>
                <a-button type="primary" :disabled="formData.name === '' || formData.password === ''" @click="handleSubmit">
                    登录
                </a-button>
            </a-form-item>
        </a-form>
    </div>
</template>

<script>
import { UserOutlined, LockOutlined } from '@ant-design/icons-vue'
import { getCurrentInstance, reactive, toRaw } from 'vue'
import { useForm } from '@ant-design-vue/use'
export default {
    components: {
        UserOutlined,
        LockOutlined,
    },
    setup() {
        //  拿到当前组件 this(ctx)
        const { ctx } = getCurrentInstance()
        const formData = reactive({
            name: 'hjd',
            password: '123456',
        })

        const rules = reactive({
            name: [
                { required: true, message: '请输入账户名称', trigger: 'blur' },
                {
                    min: 3,
                    max: 5,
                    message: '长度在 3 到 5 个字符',
                    trigger: 'blur',
                },
            ],
            password: [
                {
                    required: true,
                    message: '请输入账户密码',
                    trigger: 'blur',
                },
            ],
        })
        const { validate, validateInfos } = useForm(formData, rules)

        const handleSubmit = () => {
            validate()
                .then(() => {
                    console.log(toRaw(formData))
                    console.log(formData)
                    login()
                })
                .catch((err) => {
                    console.log('error', err)
                })
        }

        const login = () => {
            ctx.$api.user.login(formData).then(async (res) => {
                localStorage.setItem('token', res.data.token)
                ctx.$store.commit('setUserId', res.data.userInfo.id)
                await ctx.$store.dispatch('getUserInfo')
                ctx.$router.push({ path: '/home' })
            })
        }

        return { formData, rules, validate, validateInfos, handleSubmit, login }
    },
}
</script>

<style lang="scss">
.login-wrapper {
    width: 100%;
    height: 100%;
    background-image: url('../assets/bg.jpg');
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;

    .formData {
        width: 300px;
        background: rgba(255, 255, 255, 0.25);
        padding: 16px;
        border-radius: 4px;
    }
}
</style>
